export default {
  name: "video_servers",
  props: {
    online_video: undefined,
    topic_data: undefined,
    select_server_name: undefined
  },
  data: function () {
    return {
      this_video_servers: undefined,
    }
  },
  created() {
    this.this_video_servers = Object.assign({}, this.$xx_cdn_conf[this.online_video.space_name]);
    console.log('this_video_servers:', this.this_video_servers);
    if (!this.select_server_name) {
      this.select_server_name = localStorage.getItem('select_server_name');
    }
    if (!this.select_server_name) {
      this.select_server_name = this.$config.defalut_video_server;
    }

    if (this.select_server_name == 'vip') {
      if (!this.$store.state.hasLogin) {
        this.select_server_name = this.$config.defalut_video_server;
      }
    }


    var set_text = 'VIP线路';
    if (!this.$store.state.user.userinfo || !this.$store.state.user.userinfo.is_vip) {
      set_text = 'VIP线路'
    }
    this.this_video_servers['vip'] = {
      'name': set_text,
      "list": [""]
    }
  },
  watch: {
    select_server_name(newVal) {
      this.$emit('update:select_server_name', newVal);
    }
  },
  methods: {
    on_select_sever: function (e) {

      console.log('on_select_sever:', e);


      if (e == 'vip') {
        if (!this.$store.state.hasLogin) {
          return this.$app.show_no_login_dialog();
        }

        if (!this.$store.state.user.userinfo.is_vip) {
          return this.$q.dialog({
            title: this.$l('title_board_message'),
            message: this.$l('vip.download_tip'),
            cancel: this.$l('cancel'),
            ok: this.$l('vip.buy'),
            persistent: true
          }).onOk(data => {
            this.$router.push('/buy/vip');
          });
        }

      }

      if (this.select_server_name != e) {
        this.select_server_name = e;
        if (this.$ga) {
          this.$ga.event({
            eventCategory: 'switch-server',
            eventAction: 'switch',
            eventLabel: e,
            eventValue: e
          });
        }
      }

      localStorage.setItem('select_server_name', this.select_server_name);


    }
  }
}
