import $hub from "hub-js";

export default {
  name: "reply",
  props: {
    topic_data: {},
  },
  data() {
    return {
      comment_data: {
        reply_id: '',
        comment_item_id: '',
      },
      reply_tip: '',
      reply_comment_data: undefined
    }
  },
  created() {
    console.log('this.comment_data:', this.comment_data);
    this.listeners['reply:comment:reply'] = this.$hub.on('event:reply:comment', (comment_data) => {
      console.log("on event event:reply:comment", comment_data);
      this.reply_comment_data = comment_data;
    });
  },
  destroyed() {
    this.listeners['reply:comment:reply'].off();
  },
  methods: {
    on_reply: function () {

      this.comment_data.vid = this.topic_data.id;
      this.comment_data.time = (new Date()).valueOf() / 1000;

      if (this.comment_data.message.length <= 1) {
        this.$message('评论的内容过短，不得少于1个字', {
          type: "error",
        });
        return false;
      }

      if (this.reply_comment_data) {
        this.comment_data.reply_comment_id = this.reply_comment_data['_id'];
      }

      this.$axios.post('/app/view/comment', this.comment_data).then((response_data) => {
        console.log('add new comment:', response_data)
        if (response_data) {
          var comment_data = Object.assign({}, this.comment_data);
          comment_data.comment_user = this.$store.state.user.userinfo;
          comment_data.uid = this.$store.state.user.userinfo.uid;
          comment_data._id = response_data.data.add_comment['_id'];
          this.$hub.emit('event:add:comment', comment_data);
          this.comment_data.message = '';
          this.reply_tip = '回复成功';
        }
      }).catch((error) => {
        console.log('error:', error);
        this.$message("提交评论出错！")
      });
      return false
    }
  }
}
