export default {
  name: "video_download",
  props: {
    topic_data: undefined
  },
  created() {

  },
  methods: {
    on_show_download:function (){

    },
    on_direct_download: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app.show_no_login_dialog();
      }

      if (!this.$store.state.user.userinfo.is_vip) {
        return this.$q.dialog({
          title: this.$l('title_board_message'),
          message: this.$l('vip.download_tip'),
          cancel: this.$l('cancel'),
          ok: this.$l('vip.buy'),
          persistent: true
        }).onOk(data => {
          this.$router.push('/buy/vip');
        })
      }
      this.$router.push('/download/video/' + this.topic_data.id);
    },
    on_torrent_download: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app.show_no_login_dialog();
      }
      this.$router.push('/download/torrent/' + this.topic_data.id);
    }
  }
}
