import commentobject from "../script/commentobject";

export default {
  name: "item",
  mixins: [commentobject],
  props: {
    comment_data: {},
    floor: undefined
  },
  data() {
    return {
      light_css: false
    }
  },
  created() {
    this.listeners['add:comment:item'] = this.$hub.on('event:add:comment', (comment_data) => {

    });
    this.light_css = this.comment_data.light_css;
    this.update_comment_floor(this.comment_data, this.floor);
  },
  destroyed() {
    this.listeners['add:comment:item'].off();
  },
  methods: {
    on_comment_like: function (action) {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }

      var active = false;
      if (action == 'like') {
        active = !this.is_like
      } else {
        active = !this.is_dislike
      }
      var post_data = {
        'action': action,
        'comment_id': this.comment_data['_id'],
        'active': active
      };

      this.$axios.post('/app/comment/like', post_data).then((response) => {
        console.log('/app/comment/like response:', response);
        if (action == 'like') {
          if (!this.comment_data.like_list) {
            this.comment_data.like_list = [];
          }
          if (!this.is_like) {
            this.comment_data.like_list.push(this.$store.state.user.userinfo.uid);
            this.comment_data.dislike_list.remove(this.$store.state.user.userinfo.uid);
          } else {
            this.comment_data.like_list.remove(this.$store.state.user.userinfo.uid);
          }
        } else {
          if (!this.comment_data.dislike_list) {
            this.comment_data.dislike_list = [];
          }
          if (!this.is_dislike) {
            this.comment_data.dislike_list.push(this.$store.state.user.userinfo.uid);
            this.comment_data.like_list.remove(this.$store.state.user.userinfo.uid);
          } else {
            this.comment_data.dislike_list.remove(this.$store.state.user.userinfo.uid);
          }
          this.$forceUpdate();
        }
      }).catch((error) => {
        console.log('error:', error);
      });
    }
    ,
    reply_comment_event: function () {
      this.$hub.emit('event:reply:comment', this.comment_data);
      setTimeout(() => {
        document.querySelector("#reply").scrollIntoView();
      }, 1)
    }
  },
  computed: {
    is_self:function (){
      var s = this.$store.state.hasLogin && this.$store.state.user.userinfo.uid == this.comment_user.uid;
      return s;
    },
    comment_user: function () {
      if (this.comment_data.comment_user) {
        return this.comment_data.comment_user;
      }
      return this.get_comment_user(this.comment_data.comment_uid);
    },
    reply_commnet: function () {
      console.log("call reply_commnet:", this.comment_data.reply_comment_id);
      if (this.comment_data.reply_comment_id) {
        return this.get_comment_data(this.comment_data.reply_comment_id)
      }
      return undefined
    },
    is_like: function () {
      if (this.$store.state.hasLogin) {
        var uid = this.$store.state.user.userinfo.uid;
        if (this.comment_data.like_list && this.comment_data.like_list.indexOf(uid) >= 0) {
          return true;
        }
      }
      return false;
    },
    is_dislike: function () {
      if (this.$store.state.hasLogin) {
        var uid = this.$store.state.user.userinfo.uid;
        if (this.comment_data.dislike_list && this.comment_data.dislike_list.indexOf(uid) >= 0) {
          return true;
        }
      }
      return false;
    }
  }
}
