export default {
  name: "watch_limit",
  props: {
    topic_data: undefined
  },
  created() {
    if (this.$ga) {
      this.$ga.event({
        eventCategory: 'watch_limit',
        eventAction: 'show',
        eventLabel: 'islogin',
        eventValue: this.$store.state.hasLogin ? "1" : "2"
      });
    }
  },
  computed: {
    cover_image_url: function () {
      var online_video = this.topic_data.video_info.online_videos[0];
      var hash_id = online_video.hash_id;
      var cover_image_url = this.generate_cover_url(online_video.space_name, hash_id);
      console.log("cover_image_url:", cover_image_url);
      return cover_image_url;
    }
  }
}
