import thread_manager from '@/components/forum/topic/item/manager';
import video_download from "@/components/video/item/video_download";


export default {
  name: "video_tab",
  components: {thread_manager, video_download},
  props: {
    online_video: undefined,
    topic_data: undefined
  },
  computed: {
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    }
  },
  data: function () {
    return {
      defalut_tabinfo: {
        is_like: false,
        is_dislike: false,
        is_star: false,
        like_count: 0
      }
      ,
      tabinfo: Object.assign({}, this.defalut_tabinfo),
      loaded_tabinfo: false
    }
  },
  created() {
    if (!this.online_video) {
      this.online_video = this.topic_data.video_info.online_videos[0];
    }
    this.load_tabinfo();
  },
  methods: {
    load_tabinfo: function () {
      if (!this.$store.state.hasLogin) {
        this.loaded_tabinfo = true;
        return;
      }
      console.log('call load_tabinfo');
      this.$axios.get('/app/video/tabinfo', {
        params: {
          'hash_id': this.online_video.hash_id,
        }
      }).then((response) => {
        console.log('[load_tabinfo]:', response);
        this.tabinfo = Object.assign(this.tabinfo, response.data);
        console.log('this.tabinfo :', this.tabinfo);
        this.loaded_tabinfo = true;
        this.$forceUpdate();
      }).catch((error) => {

      });
    },
    on_video_like: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      var post_data = {
        'action': !this.tabinfo.is_like,
        'hash_id': this.online_video.hash_id,
      }
      console.log('on_video_like:', post_data);
      this.$axios.post('/app/video/like', post_data).then((response) => {
        this.tabinfo.is_like = true;
        this.tabinfo.is_dislike = false;
        this.$forceUpdate();
      }).catch((error) => {
        console.log("error:", error);
      });
    },
    on_video_dislike: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app.show_no_login_dialog();
      }
      var post_data = {
        'action': !this.tabinfo.is_dislike,
        'hash_id': this.online_video.hash_id
      }
      console.log('on_video_like:', post_data);
      this.$axios.post('/app/video/dislike', post_data).then((response) => {
        this.tabinfo.is_like = true;
        this.tabinfo.is_dislike = false;
      }).catch((error) => {
        console.log("error:", error);
      });
    },
    on_video_star: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app.show_no_login_dialog();
      }
      var post_data = {
        'action': !this.tabinfo.is_star,
        'hash_id': this.online_video.hash_id
      }
      console.log('on_video_star:', post_data);
      this.$axios.post('/app/video/star', post_data).then((response) => {
        this.tabinfo.is_star = true;
        this.$forceUpdate();
      }).catch((error) => {
        console.log("error:", error);
      });
    }
  }
}
