export default {
  name: "comment",
  data() {
    return {}
  },
  methods: {
    add_comment: function ({comment_data, cb}) {

    },
    load_comment_list: function ({id, start, limit, cb}) {
      this.$axios.get('/app/view/comment', {
        params: {
          'vid': id,
          'start': start,
          'limit': limit
        }
      }).then((response) => {
        var response_data = response.data;
        if (cb) {
          cb(response_data,id,start,limit)
        }
      }).catch(function (error) {
      });
    },
    get_comment_user: function (uid) {
      if (this._g.comment_users) {
        return this._g.comment_users[uid];
      }
    },
    update_comment_users: function (comment_users) {
      if (!this._g.comment_users) {
        this._g.comment_users = {};
      }
      this._g.comment_users = Object.assign(this._g.comment_users, comment_users);
    },
    update_comment_datas: function (start, limit, id, comment_datas) {
      if (!this._g.comment_datas) {
        this._g.comment_datas = {};
      }
      var key = id + ":" + start + ":" + limit;
      console.log('update_comment_datas key:', key, comment_datas);
      this._g.comment_datas[key] = comment_datas;
      console.log('this._g.comment_datas:', this._g.comment_datas);
    },
    get_comment_data: function (id) {
      var keys = Object.keys(this._g.comment_datas);
      console.log('[get_comment_data] keys:', keys);
      for (let j = 0; j < keys.length; ++j) {
        var key = keys[j];
        for (let i = 0; i < this._g.comment_datas[key].length; ++i) {
          if (this._g.comment_datas[key][i]['_id'] == id) {
            console.log('[get_comment_data]', id, this._g.comment_datas[key][i]);
            return this._g.comment_datas[key][i];
          }
        }
      }
    },
    update_comment_floor: function (comment_data, floor) {
      if (!this._g.comment_floors) {
        this._g.comment_floors = {};
      }
      this._g.comment_floors[comment_data['_id']] = floor;
    },
    get_comment_floor: function (id) {
      if (!this._g.comment_floors) {
        return undefined;
      }
      return this._g.comment_floors[id]
    }
  }
}
