import commentobject from "../script/commentobject";
import comment_item from "./item";

export default {
  mixins: [commentobject],
  components: {
    comment_item
  },
  name: "list",
  props: {
    topic_data: {},
  },
  data() {
    return {
      comment_data: {},
      all_comment_users: {}
    }
  },
  created() {
    console.log('create comment list:',this.topic_data);
    this.load_comment_list({
      id: this.topic_data.id,
      start: 0,
      limit: 36,
      cb: this.on_comment_list
    })
    this.listeners['add:comment:list'] = this.$hub.on('event:add:comment', (comment_data) => {
      console.log("on dd:comment:", comment_data);
      comment_data.light_css = true;
      this.comment_data.comments.push(comment_data);
    });
  },
  methods: {
    on_comment_list: function (comment_data, id, start, limit) {
      console.log('on_comment_list:', comment_data);
      var comment_users = comment_data.comment_users;
      this.update_comment_users(comment_users);
      this.update_comment_datas(start, limit, id, comment_data.comments);
      this.comment_data = comment_data;
      this.goto_anchor(() => {
        this.show_assgin_reply();
      });
    },
    show_assgin_reply: function () {
      var reg = /reply-([a-z0-9]+)/;
      var t_r = reg.exec(location.hash);
      console.log('t_r:', t_r);
      if (reg.test(location.hash)) {
        var strong_id = t_r[1];
      }
    }
  },
  destroyed() {
    if (this.listeners['add:comment:list']) {
      this.listeners['add:comment:list'].off();
    }
  }
}
